.container {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 15px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: antiquewhite;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}
