.btnMore {
  display: flex;
  justify-content: center;
}
.section {
  height: 40px;
  display: flex;
  justify-content: center;

  background-color: antiquewhite;
  padding-right: 15px;
  align-items: center;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

.listTypes {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 20px;

  padding-left: 30px;
  padding-right: 30px;

  &_button {
    margin-right: 10px;
    margin-bottom: 10px;
    color: #fff;
    border: none;
    border-radius: 10px;
    padding: 5px;
    cursor: pointer;
  }
}
